import classNames from 'classnames';
import styles from './styles';

const SummaryItem = ({ question }) => {
    return (
        <a className="summary-item panel-block is-active">
            Pregunta #{que}
            <style jsx>{styles}</style>
        </a>
    );
};

const Summary = ({ questions }) => (
    <nav className="panel summary">
        <p className="panel-heading">Preguntas resueltas</p>

        {
            questions.map(question => (
                <SummaryItem key={`summary-item-${question.id}`} question={question} />
            ))
        }

        <style jsx>{styles}</style>
    </nav>
);

export default Summary;
